.more-photo {
  text-align: center;
  position: relative;
}
.more__title {
  text-transform: uppercase;
  color: $titleColor;
  font-size: 24px;
  margin-bottom: 16px;
}

.mobil-list {
  margin-bottom: 32px;
}

.mobil-list__photo:not(:last-child) {
  margin-bottom: 16px;
}
.more-photo__icon {
  width: 45px;
  height: 45px;
  fill: $titleColor;
  margin-bottom: 76px;
}

.more-photo__list {
  display: none;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(43, 43, 43, 0.8);
  display: none;
  // transform: translateX(100%);
  // transition: transform 250ms linear;

  overflow: auto;

  .mobil-list__link:hover > &,
  .mobil-list__link:focus > & {
    display: block;
  }
}

.overlay__img {
  position: absolute;
  top: 50%;
  left: 30%;
}
@media screen and (min-width: 767px) {
  .mobil-list {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .more__title {
    text-align: initial;
    font-size: 36px;
    margin-bottom: 32px;
  }

  .more-photo__list {
    display: block;
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;
    margin-top: -20px;
    margin-bottom: 32px;
  }

  .more-photo__item {
    margin-left: 19px;
    margin-top: 20px;
    margin-right: 0;
  }

  .more-photo__icon {
    margin-bottom: 52px;
  }
}

@media screen and (min-width: 1440px) {
  .more__title {
    margin-bottom: 52px;
  }

  .more-photo__list {
    margin-left: -32px;
    margin-top: -32px;
    margin-bottom: 56px;
  }

  .more-photo__item {
    margin-left: 31px;
    margin-top: 32px;
  }

  .more-photo__icon {
    margin-bottom: 100px;
  }
}
